/**
 * 班级整体学习情况
 */
import React from "react";
import "../css/classLearning.css";
// 导入组件
import LineClass from "./classLearn-children/line"
import ClassLearning_Calendar from "./classLearn-children/classLearning-calendar"
// 导入其他包
import {Select, DatePicker} from "antd"
import "antd/dist/reset.css"
// dayjs包
import dayjs from "dayjs";
import isLeapYear from "dayjs/plugin/isLeapYear";
import "dayjs/locale/zh-cn"

interface ClassLearingProps{}
interface ClassLearingState{
    Teaching_class_now: number // 当前课时 
    Teaching_class_time_now: any[] // 当前课时持续时间
}

function ClassLearing(props: ClassLearingProps, state: ClassLearingState){
    // 启动dayjs插件
    dayjs.extend(isLeapYear)
    dayjs.locale("zh-cn")

    /**
     * 常量
     */
    const Teaching_class = [
        "第一课时",
        "第二课时",
        "第三课时",
        "第四课时",
        "第五课时",
        "第六课时",
        "第七课时",
        "第八课时",
    ]
    const Teaching_class_time = [
        ["2022-2-14", "2022-2-24"],
        ["2022-2-25", "2022-3-10"],
        ["2022-3-11", "2022-3-31"],
        ["2022-4-1", "2022-4-20"],
        ["2022-4-21", "2022-5-5"],
        ["2022-5-6", "2022-5-25"],
        ["2022-5-26", "2022-6-10"],
        ["2022-6-11", "2022-6-30"],
    ]
    const Teaching_class_option = Teaching_class.map((v, index) => {
        return {value: index, label: v}
    })
    const {RangePicker} = DatePicker

    // state
    const [Teaching_class_now, Set_Teaching_class_now] = React.useState(0)
    const [Teaching_class_time_now, Set_Teaching_class_time_now] = React.useState([dayjs(new Date(Teaching_class_time[0][0])), dayjs(new Date(Teaching_class_time[0][1]))])

    // React.useEffect(() => {
    //     console.log(Teaching_class_now);
    // }, [Teaching_class_now])

    return (
        <div className="ClassLearing">
            <div className="ClassLearning_two">
                {/* 课时及日期 */}
                <div style={{borderBottom: '1px solid rgb(230, 230, 230)'}}>
                    <div>
                        {/* &nbsp; */}
                        <Select
                            style={{width: 95, height: 25, lineHeight: "25px", float: 'left', marginLeft: 20, marginTop: 7.5}}
                            size="small"
                            defaultValue={Teaching_class[0]}
                            options={Teaching_class_option}
                            bordered={false}
                            onChange={(value) => {
                                Set_Teaching_class_now(parseInt(value))
                                Set_Teaching_class_time_now([dayjs(new Date(Teaching_class_time[parseInt(value)][0])), dayjs(new Date(Teaching_class_time[parseInt(value)][1]))])
                            }}
                        ></Select>
                    </div>
                    <div>
                        {/* 这里的日期格式为 dayjs */}
                        <RangePicker
                            size="small"
                            style={{float: 'right', width: '100%', height: 25, marginTop: 7.5, marginRight: 10, border: '1px solid rgb(170, 170, 170)'}}
                            // bordered={false}
                            value={Teaching_class_time_now as any}
                            allowClear={false}
                            inputReadOnly={true}
                            open={false}
                        ></RangePicker>
                    </div>
                </div>
                {/* 日历图、雷达图 */}
                <div>
                    {/* 日历图 */}
                    {/* <div className="ClassLearning_Calendar"></div> */}
                    <ClassLearning_Calendar></ClassLearning_Calendar>
                    {/* 雷达图 */}
                    <div className="ClassLearning_Radar"></div>
                </div>
            </div>
            {/* <ClassLearning_Calendar></ClassLearning_Calendar>
            <div className="ClassLearning_Radar"></div> */}
            <div className="ClassLearning_WordCloud"></div>
            <div className="ClassLearning_Tree"></div>
            <div className="ClassLearning_Line"></div>
            <div className="ClassLearning_Suggest"></div>
        </div>
    )
}

export default ClassLearing