/**
 * 日历图
 */
import React from "react"
import * as d3 from "d3"
import "./css/calendar.css"

interface ClassLearning_CalendarProps{}
interface ClassLearning_CalendarState{}

function ClassLearning_Calendar(props: ClassLearning_CalendarProps, state: ClassLearning_CalendarState){
    /**
     * 绘制日历图
     */
    function DrawCalendar(){
        d3.select("#ClassLearning_Calendar_svg").selectAll("g").remove()
        let svg = d3.select("#ClassLearning_Calendar_svg")
        .append('g')
        .attr('viewBox', "0 0 400 400")
        svg.append("rect")
        .attr("x", 30)
        .attr('y', 30)
        .attr('width', 300)
        .attr('height', 200)
        .attr('fill', 'blue')
    }

    React.useEffect(() => {
        DrawCalendar()
    }, [])

    return(
        <div className="ClassLearning_Calendar">
            <div>学生日活跃度</div>
            <div>
                <svg id="ClassLearning_Calendar_svg"></svg>
            </div>
        </div>
    )
}

export default ClassLearning_Calendar